<template>
  <div class="count_down_line">
    <div class="count_down_item">{{time_left.h}}</div>
    <span class="count_down_space">:</span>
    <div class="count_down_item">{{time_left.m}}</div>
    <span class="count_down_space">:</span>
    <div class="count_down_item">{{time_left.s}}</div>
    <!-- dev begin -->
     <!-- dev end   -->
  </div>
</template>

<script>
const end_time  = {h: 10, m: 0, s: 0}
export default {
  name: 'countDownTime',
  data(){
    return {
      end_time: end_time,
      time_left: {h: 0, m: 0, s: 0},
      second_left: 0 
    } 
  },
  methods: {
    cal_time(current_time){
      if( this.getSecond(current_time) < this.getSecond(this.end_time) ){
        return this.second_left = this.getSecond(this.end_time) - this.getSecond(current_time) 
      }else{
        return this.second_left = 24 * 3600 - this.getSecond(current_time)  + 10 * 3600
      }
    }, 
    getSecond(date_time){
      var second = date_time.h * 3600 + date_time.m * 60 + date_time.s;
      return second;
    },
    getDateTime(second){
      var h = Math.floor(second / 3600);
      var m = Math.floor(Math.floor( second % 3600 ) / 60)
      var s = Math.floor(Math.floor( second % 3600 ) % 60)
      return { h: h, m: m, s: s }
    }
  },
  created(){
    var that = this;
    setInterval(function(){
      var current_date = new Date() 
      var current_time = {
        h: current_date.getHours(),
        m: current_date.getMinutes(),
        s: current_date.getSeconds()
      }
      that.time_left = that.getDateTime(that.cal_time(current_time))
    },1000)
  }
}
</script>

<style scoped>
.count_down_line{
  text-align: center;
}
.count_down_item{
  display: inline-block;
  color: white;
  background-color: #333333;
  padding:4px 2px;
  width: 1rem;
}
.count_down_space{
  margin: 0px;
}


</style>




