<template>
  <div id="banner">
    <img :src=banner class="header" >
    <div id="content">

      <!-- sub event begin -->
      <div id="event_new_people" class="event">
          <img class="event_header_img" 
               :src=event_imgs[0]> 

          <div>
            <div class="event_item" v-for="i in section_new_gift">
              <div class="event_header_img_container"> 
                <img class="event_item_img" :src=i.image> 
              </div>
              <div class="event_item_name"
                   id="event_new_people_item_name">
                   {{i.productName}}
              </div>
            </div> 
          </div>
      </div>
      <!-- sub event end   -->


      <!-- sub event begin -->
      <div class="time_count_down">每天10:00 准时开秒</div>
      <div class="event"
           style="margin-top:0px !important;padding-top:5vw;">
          <!-- count down begin -->
          <countDownTime></countDownTime>
           <!-- count down end -->
          <div class="event_content">
            <div class="event_item" v-for="i in section_time">
              <img class="event_item_img" :src=i.image> 
              <div class="event_item_name">{{i.productName}}</div>
              <div class="event_item_origin_price">¥{{i.salesPrice}}</div>
              <div class="event_item_current_price">¥{{i.referencePrice}}</div>
              <button class="event_item_btn"
                      @click=product_detail(i.id)>
                立即秒杀
              </button>
            </div> 
          </div>
      </div>
      <!-- sub event end   -->

      <!-- sub event begin -->
      <div class="event">
          <img class="event_header_img" :src=event_imgs[1]> 
          <div class="event_content">
            <div class="event_item" v-for="i in section_1">
              <img class="event_item_img" :src=i.image> 
              <div class="event_item_name">{{i.productName}}</div>
              <div class="event_item_origin_price">¥{{i.salesPrice}}</div>
              <div class="event_item_current_price">¥{{i.referencePrice}}</div>
              <button class="event_item_btn"
                      @click=product_detail(i.id)>
                立即购买
              </button>
            </div> 
          </div>
      </div>
      <!-- sub event end   -->


      <!-- sub event begin -->
      <div class="event">
          <img class="event_header_img" :src=event_imgs[2]> 
          <div class="event_content">
            <div class="event_item" v-for="i in section_2">
              <img class="event_item_img" :src=i.image> 
              <div class="event_item_name">{{i.productName}}</div>
              <div class="event_item_origin_price">¥{{i.salesPrice}}</div>
              <div class="event_item_current_price">¥{{i.referencePrice}}</div>
              <button class="event_item_btn"
                      @click=product_detail(i.id)>
                立即购买
              </button>
            </div> 
          </div>
      </div>
      <!-- sub event end   -->

      <!-- sub event begin -->
      <div class="event">
          <img class="event_header_img" :src=event_imgs[3]> 
          <div class="event_content">
            <div class="event_item" v-for="i in section_3">
              <img class="event_item_img" :src=i.image> 
              <div class="event_item_name">{{i.productName}}</div>
              <div class="event_item_origin_price">¥{{i.salesPrice}}</div>
              <div class="event_item_current_price">¥{{i.referencePrice}}</div>
              <button class="event_item_btn"
                      @click=product_detail(i.id)>
                立即购买
              </button>
            </div> 
          </div>
      </div>
      <!-- sub event end   -->


    </div>
  </div>
</template>

<script>

const banner = "./static/banner/header.jpg";

const event_1 = "./static/banner/1.png";
const event_2 = "./static/banner/2.png";
const event_3 = "./static/banner/3.png";
const event_4 = "./static/banner/4.png";

import countDownTime from '../components/countDownTime';

import Product_service from '../api/products.js';

export default {
  name: 'banner',
  data(){
    return {
      banner: banner,
      section_types: [1, 2, 3],
      section_new_gift: [],
      section_time: [],
      section_1: [],
      section_2: [],
      section_3: [],
      event_imgs: [ event_1, event_2, event_3, event_4 ]
    } 
  },
  computed: {
    present_date_time(){
      return this.time_left; 
    } 
  },
  methods: {
    get_products(type, call_back){
      Product_service.get_products(type, call_back);
    },
    product_detail(id){
      location.href = "http://mall.huishoubao.com/#/project/goods/detail?id=" + id;
    }
  }, 
  components: {
    'countDownTime': countDownTime 
  },
  created: function(){
    var that = this;

    this.get_products(7, function(resp){
      that.section_new_gift = resp.list;
    })

    this.get_products(10, function(resp){
      that.section_time = resp.list;
    })

    this.get_products(11, function(resp){
      that.section_1 = resp.list;
    })

    this.get_products(12, function(resp){
      that.section_2 = resp.list;
    })

    this.get_products(13, function(resp){
      that.section_3 = resp.list;
    })

  }
}
</script>

<style scoped>
#banner{
  background-color: #ff465b;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow: auto;
}
.header{
  width: 100vw;
}
#content{
  padding-top: 10vw;
  //background-color: #efefef;
  position: absolute;
  overflow: auto;
  width: 100vw;
  top: 80vw;
  padding-bottom: 10vw;
}
.event{
  position: relative;
  width: 90vw;
  margin: 5vw;
  border: 1px solid transparent;
  border-radius: 10px;
  background-color: white;
  padding: 13vw 0vw 5vw 0vw;
  margin-bottom: 20vw;
}
.event_header_img{
  position: absolute;
  width: 50vw;
  margin-left: 20vw;
  top: -10vw;
}
#event_new_people_header_img{
}
.event_content{
  display: block;
  background-color: white;
  border-radius: 14px;
}
.event_item{
  width: 24vw;
  margin: 0vw 3vw;
  display: inline-block;
}
.event_item_img{
  width: 100%;
  display: block;
  margin: 0px auto;
}
.event_item_name{
  font-size: 1rem;
  color: #333333;
  margin: .2rem 0rem;
  text-align: center;
}
#event_new_people_item_name{
  color: white;
  margin: 1rem 0rem 0rem 0rem;
}
.event_item_origin_price{
  display: inline-block;
  font-size: .9rem;
  color: #E20A33;
  margin: .2rem 0rem;
}
.event_item_current_price{
  display: inline-block;
  font-size: .8rem;
  color: #333333;
  text-decoration: line-through;
  margin: .2rem 0rem;
  margin-left: 2px;
  float: right;
}
.event_item_btn{
  color: white;
  background-color: #F12A3D;
  border-radius: 4px;
  border: 1px solid transparent;
  width: 100%;
  padding: 4px 0px;
  outline: none;
}
.time_count_down{
  display: block;
  text-align: center;
  color: white;
  font-size: 1.3rem;
  padding: 1rem;
}
#event_new_people{
  border: 3px solid #C60013;
  background-color: #FE7383;
  margin-bottom: 8vw;
}
.event_header_img_container{
  border-radius: 8px;
  padding: 5px;
  background-color: white;
}
</style>




